.loader {
    width: 500px;
    padding: 8px;
    aspect-ratio: 1;
    border-radius: 50%;
    background: radial-gradient(81.5% 240.42% at 50% -45.06%, #41ddff 23.88%, #738aff 38.18%, #acafff 43.57%, #fff 72.09%, #fff 95.27%);
    --_m: 
      conic-gradient(#0000 10%,#000),
      linear-gradient(#000 0 0) content-box;
    -webkit-mask: var(--_m);
            mask: var(--_m);
    -webkit-mask-composite: source-out;
            mask-composite: subtract;
    animation: l3 1s infinite linear;
  }
  @keyframes l3 {to{transform: rotate(1turn)}}